<template>
  <div class="section-blue">
    <md-dialog-confirm
      :md-active.sync="showDialog"
      :md-title="dialogTittle"
      :md-content="dialogContent" />
    <md-dialog-confirm
      :md-active.sync="confirmDialog"
      :md-title="dialogTittle"
      :md-content="dialogContent"
      md-confirm-text="Agree"
      md-cancel-text="Disagree"
      :md-click-outside-to-close="false"
      @md-cancel="onCancel"
      @md-confirm="onConfirm" />
    <md-snackbar :md-position="position" :md-duration="isInfinity ? Infinity : duration" :md-active.sync="showSnackbar" md-persistent>
      <span>{{ snackbarText }}</span>
      <md-button class="md-primary" @click="showSnackbar = false">Retry</md-button>
    </md-snackbar>
    <div class="md-layout md-gutter md-alignment-center">
      <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
        <h1 class="secondary-tittle">Matches</h1>
      </div>
    </div>
    <div class="md-layout md-gutter md-alignment-center">
      <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
        <md-content :md-elevation="3" class="button-card" id="rapid_access">
          <div class="spacing">
            <h2>Fast Access</h2>
            <div class="md-layout md-gutter md-alignment-center">
              <template v-for="array in bigArray">
                <div class="md-layout-item md-size-20 md-large-size-20 md-medium-size-20 md-small-size-20 md-xsmall-size-20 align-button" v-for="gmatch in array" :key="gmatch.name">
                  <md-button class="md-raised md-primary group-button" :href="getLinkId(gmatch.name)">{{ gmatch.name }}</md-button>
                </div>
              </template>
            </div>
            <h2>Predicts</h2>
            <div class="md-layout md-gutter md-alignment-center">
              <div class="md-layout-item md-size-20 md-large-size-20 md-medium-size-20 md-small-size-20 md-xsmall-size-20 align-button">
                <md-button class="md-raised md-primary group-button" @click="makePrediction">Make your prediction</md-button>
              </div>
              <div class="md-layout-item md-size-20 md-large-size-20 md-medium-size-20 md-small-size-20 md-xsmall-size-20 align-button">
                <md-button class="md-raised md-primary group-button" @click="savePrediction">Save your prediction</md-button>
              </div>
              <div class="md-layout-item md-size-20 md-large-size-20 md-medium-size-20 md-small-size-20 md-xsmall-size-20 align-button">
                <md-button class="md-raised md-primary group-button" @click="loadPrediction">Load your prediction</md-button>
              </div>
              <div class="md-layout-item md-size-20 md-large-size-20 md-medium-size-20 md-small-size-20 md-xsmall-size-20 align-button">
                <md-button class="md-raised md-primary group-button" @click="clearPrediction">Clear your prediction</md-button>
              </div>
            </div>
          </div>
        </md-content>
      </div>
    </div>
    <div class="md-layout md-gutter md-alignment-center">
      <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
        <div class="md-layout md-gutter md-alignment-center align-cards">
          <template v-for="array in bigArray">
            <div class="md-layout-item md-size-100 md-large-size-100 md-medium-size-100 md-small-size-100 md-xsmall-size-100 group-card" v-for="gmatch in array" :key="gmatch.name">
              <div class="viewport">
                <md-toolbar :md-elevation="1" class="md-primary md-alignment-center" :id="formatId(gmatch.name)">
                  <span class="md-title md-alignment-center title-group">{{ gmatch.name }}</span>
                  <md-button class="md-icon-button" href="#rapid_access">
                    <md-icon>apps</md-icon>
                  </md-button>
                </md-toolbar>

                <md-list>
                  <md-list-item v-for="match in gmatch.matches" :key="match.name" class="border">
                    <div class="md-layout md-gutter">
                      <div class="md-layout-item md-size-25 md-small-size-40 md-xsmall-size-100 info-left">
                        <div><span class="md-list-item-text">{{ formatDate(match.date) }}</span></div>
                        <div><span class="md-list-item-text">{{ getStadium(match.stadium).name }}</span></div>
                        <div><span class="md-list-item-text">{{ getStadium(match.stadium).city }}</span></div>
                      </div>
                      <div class="md-layout-item md-small-size-60 md-xsmall-size-100 info-center">
                        <md-list>
                          <md-list-item>
                            <span class="md-list-item-text center-text">{{ formatTitle(match.type, match.matchday) }}</span>
                          </md-list-item>
                          <md-list-item>
                            <span class="md-list-item-text hteam-text">{{ getTeam(match.home_team).name }}</span>
                            <img :src="getTeam(match.home_team).flag" :alt="getTeam(match.home_team).emoji" class="team-flag">
                            <md-field class="md-list-item-text hteam-text input-result" v-if="editResults">
                              <label>Type Here</label>
                              <md-input v-model.number.lazy="match.home_result" min="0" type="number" pattern="[0-9]*"></md-input>
                            </md-field>
                            <span class="md-list-item-text hteam-text result-text" v-if="!editResults">{{ match.home_result || '0' }}</span>
                            <span class="md-list-item-text versus-text">{{ ' X ' }}</span>
                            <span class="md-list-item-text ateam-text result-text" v-if="!editResults">{{ match.away_result || '0' }}</span>
                            <md-field class="md-list-item-text ateam-text input-result" v-if="editResults">
                              <label>Type Here</label>
                              <md-input v-model.number.lazy="match.away_result" min="0" type="number" pattern="[0-9]*"></md-input>
                            </md-field>
                            <img :src="getTeam(match.away_team).flag" :alt="getTeam(match.away_team).emoji" class="team-flag">
                            <span class="md-list-item-text ateam-text">{{ getTeam(match.away_team).name }}</span>
                          </md-list-item>
                          <md-list-item>
                            <span class="md-list-item-text center-text">{{ match.finished ? 'Finished' : 'Not Finished' }}</span>
                          </md-list-item>
                        </md-list>
                      </div>
                      <div class="md-layout-item md-size-25 md-small-hide info-right">
                        <div><span class="md-list-item-text">Weather Conditions</span></div>
                        <div><span class="md-list-item-text">{{ getWeatherByCity(getStadium(match.stadium).id).weather[0].description }}</span></div>
                        <div><span class="md-list-item-text">{{ 'Temperature: ' + getWeatherByCity(getStadium(match.stadium).id).main.temp + ' C' }}</span></div>
                        <div><span class="md-list-item-text">{{ 'Humidity: ' + getWeatherByCity(getStadium(match.stadium).id).main.humidity + '%' }}</span></div>
                        <div><span class="md-list-item-text">{{ 'Wind Speed: ' + (getWeatherByCity(getStadium(match.stadium).id).wind.speed) * 3.6 + ' km/h' }}</span></div>
                        <div>
                          <span class="md-list-item-text inline-icon">{{ 'Wind Direction: ' }}</span>
                          <md-icon class="wind-icon" :style="{ transform: 'rotate(' + getWeatherByCity(getStadium(match.stadium).id).wind.deg + 'deg)' }">arrow_right_alt</md-icon>
                        </div>
                      </div>
                    </div>
                  </md-list-item>
                </md-list>
                <md-list>
                  <md-list-item>
                    <div class="md-list-item-text">
                      <span>Qualified Teams</span>
                    </div>
                  </md-list-item>
                  <md-list-item>
                    <md-icon class="md-primary">looks_one</md-icon>
                    <div class="md-list-item-text">
                      <span>{{ gmatch.winner ? gmatch.winner : 'To be defined' }}</span>
                    </div>
                  </md-list-item>
                  <md-list-item>
                    <md-icon class="md-primary">looks_two</md-icon>
                    <div class="md-list-item-text">
                      <span>{{ gmatch.runnerup ? gmatch.runnerup : 'To be defined' }}</span>
                    </div>
                  </md-list-item>
                </md-list>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
    <div class="section-gold">
      <div class="md-layout md-gutter md-alignment-center">
        <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
          <Footer></Footer>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import Footer from './Footer'

export default {
  name: 'Matches',
  components: {
    Footer
  },
  data () {
    return {
      bigArray: [],
      teams: [],
      stadiums: [],
      gmatches: [],
      knockout: [],
      errors: [],
      errorsWeather: [],
      weatherInfo: [],
      backupArray: [],
      counter: 0,
      editResults: false,
      showDialog: false,
      confirmDialog: false,
      dialogTittle: 'Predict',
      dialogContent: '',
      showSnackbar: false,
      position: 'center',
      duration: 4000,
      isInfinity: false,
      snackbarText: ''
    }
  },
  created () {
    Axios.get(`https://raw.githubusercontent.com/lsv/fifa-worldcup-2018/master/data.json`)
      .then(response => {
        this.teams = response.data.teams
        this.stadiums = response.data.stadiums
        this.gmatches = response.data.groups
        this.knockout = response.data.knockout
        this.bigArray.push(this.gmatches)
        this.bigArray.push(this.knockout)
        this.backupArray = Object.assign({}, this.bigArray)
        this.getWeatherInfo()
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  methods: {
    formatDate: function (date) {
      let time = new Date(date) + ''
      let v = time.split(' ')
      let formated = v[2] + '/' + v[1] + '/' + v[3] + ' - ' + v[4]
      return formated
    },
    getStadium: function (id) {
      for (let stadium of this.stadiums) {
        if (id === stadium.id) {
          return stadium
        }
      }
      return null
    },
    getTeam: function (id) {
      for (let team of this.teams) {
        if (id === team.id) {
          return team
        }
      }
      return {flag: '', emoji: '', name: id}
    },
    formatTitle: function (type, round) {
      let ftype = ''
      if (type === 'group') {
        ftype = 'Group Phase'
      } else {
        ftype = 'Knockout Phase'
      }
      return ftype + ' - Round ' + round
    },
    getWeatherInfo: function () {
      let weatherLocal = JSON.parse(sessionStorage.getItem('weatherArray')) || null
      if (weatherLocal !== null) {
        this.weatherInfo = weatherLocal
      } else {
        Axios.get(`http://api.openweathermap.org/data/2.5/group?id=524901,524901,498817,554234,551487,520555,499099,472757,498698,501175,491422,1486209&units=metric&appid=5d45ad93dce8952c5fa4330170c57d16
`)
          .then(response => {
            this.weatherInfo = response.data
            if (typeof (Storage) !== 'undefined') {
              sessionStorage.setItem('weatherArray', JSON.stringify(this.weatherInfo))
            }
          })
          .catch(e => {
            this.errorsWeather.push(e)
          })
      }
    },
    getWeatherByCity: function (id) {
      return this.weatherInfo.list[id - 1]
    },
    formatId: function (name) {
      let str = name.split(' ')
      let formatted = ''
      for (let i = 0; i < str.length; i++) {
        formatted += str[i] + '_'
      }
      return formatted.toLowerCase()
    },
    getLinkId: function (link) {
      return '#' + this.formatId(link)
    },
    checkStorageAvailable: function () {
      if (typeof (Storage) !== 'undefined') {
        return true
      } else {
        return false
      }
    },
    makePrediction: function () {
      if (this.checkStorageAvailable) {
        this.dialogContent = 'Make your results predictions in the following inputs on each match. After this click on the save button to storage your predictions.'
        this.showDialog = true
        this.editResults = true
      } else {
        this.errorDialog()
      }
    },
    savePrediction: function () {
      if (this.checkStorageAvailable) {
        this.dialogContent = 'Are you sure you want to save? To load the predictions, click on the load button.'
        this.confirmDialog = true
        this.editResults = true
      } else {
        this.errorDialog()
      }
    },
    loadPrediction: function () {
      if (this.checkStorageAvailable) {
        let loadArray = JSON.parse(localStorage.getItem('predictionArray')) || null
        if (loadArray !== null) {
          this.bigArray = loadArray
          this.dialogContent = 'Your prediction are loading, please wait.'
        } else {
          this.dialogContent = 'No prediction results were found.'
        }
        this.editResults = false
        this.showDialog = true
      } else {
        this.errorDialog()
      }
    },
    clearPrediction: function () {
      if (this.checkStorageAvailable) {
        localStorage.removeItem('predictionArray')
        this.bigArray = this.backupArray
        this.dialogContent = 'Your prediction results were removed.'
        this.showDialog = true
        this.editResults = false
      } else {
        this.errorDialog()
      }
    },
    onConfirm () {
      localStorage.setItem('predictionArray', JSON.stringify(this.bigArray))
      this.snackbarText = 'Thanks! Your predictions were save.'
      this.showSnackbar = true
    },
    onCancel () {
      this.snackbarText = 'Atention! Your predictions were not save.'
      this.showSnackbar = true
    },
    errorDialog: function () {
      this.dialogTittle = 'Error'
      this.dialogContent = 'Sorry. Your browser does not support this feature. Please try again in other!'
      this.showDialog = true
      this.editResults = false
    }
  }
}
</script>

<style scoped>
.team-flag {
  border-style: solid;
  border-width: 3px;
  border-color: #E5C685;
  border-radius: 5px;
  width: 32px;
  height: 22px;
  margin: 5px;
}
.group-card {
  margin-top: 10px;
  margin-bottom: 25px;
}
h1 {
  font-size: 7em;
  line-height: 1em;
  text-transform: uppercase;
}
.align-center {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  max-width: 1280px !important;
  width: 100% !important;
}
.align-cards {
  align-items: start !important;
  margin: 10px;
}
.section-gold {
  background-color: #F5EED5;
}
.section-blue {
  background:
    linear-gradient(
      rgba(1, 83, 134, 0.85),
      rgba(1, 83, 134, 0.85)
    ),
    url(http://cdn.welcome2018.com//upload/longreads/2310989/img/tild6339-3063-4766-a332-616639393330__1920x1080_pearl_flat.png?15241335931291351) repeat;
}
.secondary-tittle {
  color: #E5C685 !important;
}
.footer {
  text-align: center;
}
.info-left {
  align-items: flex-start !important;
}
.info-center {
  align-items: center !important;
}
.info-right {
  align-items: flex-end !important;
  text-align: -webkit-right;
}
.md-list-item-container {
  font-size: 20px;
}
.hteam-text {
  align-items: flex-end;
}
.versus-text {
  align-items: center;
  max-width: 20px;
}
.ateam-text {
  align-items: flex-start;
}
.result-text {
  max-width: 25px;
}
.center-text {
  align-items: center;
}
div.md-ripple.md-list-item-content.md-disabled {
  min-height: 0px !important;
}
.border {
  border-bottom: 1px solid var(--md-theme-default-divider, rgba(0,0,0,0.12));
}
.inline-icon {
  display: inline !important;
}
.wind-icon {
  color: rgba(0,0,0,0.87) !important;
}
.align-button {
  text-align: center;
}
.button-card {
  margin: 30px;
}
h2 {
  color: #015386;
  font-size: 40px;
  text-align: center;
}
.spacing {
  padding: 25px;
}
.group-button {
  font-size: 18px !important;
  min-width: 142px;
}
.title-group {
  flex: 1
}
.md-toolbar {
  background-color: #E5C685 !important;
}
.input-result {
  max-width: 60px;
}
</style>



