<template>
  <div class="section-blue">
    <div class="md-layout md-gutter md-alignment-center">
      <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
        <h1 class="secondary-tittle">Standings</h1>
      </div>
    </div>
    <div class="md-layout md-gutter md-alignment-center">
      <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
        <div class="md-layout md-gutter md-alignment-center align-cards">
          <div class="md-layout-item md-size-100 md-large-size-100 md-medium-size-100 md-small-size-100 md-xsmall-size-100 group-card" v-for="group in groups" :key="group.id">
            <md-table md-card ref="specialrow">
              <md-table-toolbar>
                <h1 class="md-title">{{ group.name }}</h1>
              </md-table-toolbar>

              <md-table-row>
                <md-table-head>Team</md-table-head>
                <md-table-head md-numeric>MP</md-table-head>
                <md-table-head md-numeric class="md-small-hide">W</md-table-head>
                <md-table-head md-numeric class="md-small-hide">D</md-table-head>
                <md-table-head md-numeric class="md-small-hide">L</md-table-head>
                <md-table-head md-numeric class="md-xsmall-hide">GF</md-table-head>
                <md-table-head md-numeric class="md-xsmall-hide">GA</md-table-head>
                <md-table-head md-numeric>+/-</md-table-head>
                <md-table-head md-numeric>PTS</md-table-head>
              </md-table-row>

              <md-table-row v-for="team in group.teams" :key="team.id">
                <md-table-cell class="fixed-space" md-label="Name" md-sort-by="name">
                  <img :src="team.flag" :alt="team.emoji" class="team-flag">
                  <span class="md-xsmall-hide">{{ team.fifaCode }}</span>
                </md-table-cell>
                <md-table-cell class="fixed-space" md-label="mp" md-sort-by="id" md-numeric>{{ team.mp || 0 }}</md-table-cell>
                <md-table-cell class="fixed-space md-small-hide" md-label="w" md-sort-by="w" md-numeric>{{ team.w || 0 }}</md-table-cell>
                <md-table-cell class="fixed-space md-small-hide" md-label="d" md-sort-by="d" md-numeric>{{ team.d || 0 }}</md-table-cell>
                <md-table-cell class="fixed-space md-small-hide" md-label="l" md-sort-by="l" md-numeric>{{ team.l || 0 }}</md-table-cell>
                <md-table-cell class="fixed-space md-xsmall-hide" md-label="gf" md-sort-by="gf" md-numeric>{{ team.gf || 0 }}</md-table-cell>
                <md-table-cell class="fixed-space md-xsmall-hide" md-label="ga" md-sort-by="ga" md-numeric>{{ team.ga || 0 }}</md-table-cell>
                <md-table-cell class="fixed-space" md-label="dif" md-sort-by="dif" md-numeric>{{ (team.gf - team.ga) || 0 }}</md-table-cell>
                <md-table-cell class="fixed-space" md-label="pts" md-sort-by="pts" md-numeric>{{ ((team.w * 3) + (team.d * 1)) || 0 }}</md-table-cell>
              </md-table-row>
            </md-table>
          </div>
        </div>
      </div>
    </div>
    <div class="section-gold">
      <div class="md-layout md-gutter md-alignment-center">
        <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
          <Footer></Footer>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import Footer from './Footer'

export default {
  name: 'Standings',
  components: {
    Footer
  },
  data () {
    return {
      teams: [],
      groups: [],
      gmatches: [],
      errors: [],
      letters: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H'],
      begin: 0,
      end: 4,
      counter: 0
    }
  },
  created () {
    let groupsSession = JSON.parse(sessionStorage.getItem('tableArray')) || null
    if (groupsSession !== null) {
      this.groups = groupsSession
    } else {
      Axios.get(`https://raw.githubusercontent.com/lsv/fifa-worldcup-2018/master/data.json`)
        .then(response => {
          this.teams = response.data.teams
          this.gmatches = response.data.groups
          this.populate(this.teams)
          if (typeof (Storage) !== 'undefined') {
            sessionStorage.setItem('tableArray', JSON.stringify(this.groups))
          }
        })
        .catch(e => {
          this.errors.push(e)
        })
    }
  },
  mounted () {
    let results = this.$refs.specialrow
    for (let result of results) {
      result.$el.lastElementChild.children[0].childNodes[2].children[1].style.backgroundColor = '#015386'
      result.$el.lastElementChild.children[0].childNodes[2].children[1].style.color = '#ffffff'
      result.$el.lastElementChild.children[0].childNodes[2].children[2].style.backgroundColor = '#015386'
      result.$el.lastElementChild.children[0].childNodes[2].children[2].style.color = '#ffffff'
      result.$el.lastElementChild.children[0].childNodes[2].children[3].style.backgroundColor = '#E5C685'
      result.$el.lastElementChild.children[0].childNodes[2].children[4].style.backgroundColor = '#E5C685'
    }
  },
  methods: {
    populate: function (dataTeams) {
      for (let i = 0; i < this.letters.length; i++) {
        let tittle = 'Group ' + this.letters[this.counter]
        this.groups.push({id: this.counter, name: tittle, teams: dataTeams.slice(this.begin, this.end)})
        this.counter++
        this.begin += 4
        this.end += 4
      }
      for (let match of this.gmatches.a.matches) {
        this.getPoints(match, 0)
      }
      for (let match of this.gmatches.b.matches) {
        this.getPoints(match, 1)
      }
      for (let match of this.gmatches.c.matches) {
        this.getPoints(match, 2)
      }
      for (let match of this.gmatches.d.matches) {
        this.getPoints(match, 3)
      }
      for (let match of this.gmatches.e.matches) {
        this.getPoints(match, 4)
      }
      for (let match of this.gmatches.f.matches) {
        this.getPoints(match, 5)
      }
      for (let match of this.gmatches.g.matches) {
        this.getPoints(match, 6)
      }
      for (let match of this.gmatches.h.matches) {
        this.getPoints(match, 7)
      }

      for (let i = 0; i < 8; i++) {
        this.groups[i].teams.sort((a, b) => b.pts - a.pts || b.dif - a.dif || b.gf - a.gf)
      }
    },
    getTeam: function (groupIndex, id) {
      for (let team of this.groups[groupIndex].teams) {
        if (id === team.id) {
          return team
        }
      }
      return null
    },
    getPoints: function (match, groupID) {
      let homeTeam = this.getTeam(groupID, match.home_team)
      let awayTeam = this.getTeam(groupID, match.away_team)
      if (match.finished) {
        homeTeam.mp = (homeTeam.mp || 0) + 1
        awayTeam.mp = (awayTeam.mp || 0) + 1
        let homeTeamResult = match.home_result || 0
        let awayTeamResult = match.away_result || 0
        if (homeTeamResult > awayTeamResult) {
          homeTeam.w = (homeTeam.w || 0) + 1
          homeTeam.d = (homeTeam.d || 0)
          homeTeam.l = (homeTeam.l || 0)
          awayTeam.l = (awayTeam.l || 0) + 1
          awayTeam.d = (awayTeam.d || 0)
          awayTeam.w = (awayTeam.w || 0)
        } else if (awayTeamResult > homeTeamResult) {
          homeTeam.l = (homeTeam.l || 0) + 1
          homeTeam.d = (homeTeam.d || 0)
          homeTeam.w = (homeTeam.w || 0)
          awayTeam.w = (awayTeam.w || 0) + 1
          awayTeam.d = (awayTeam.d || 0)
          awayTeam.l = (awayTeam.l || 0)
        } else {
          homeTeam.d = (homeTeam.d || 0) + 1
          homeTeam.w = (homeTeam.w || 0)
          homeTeam.l = (homeTeam.l || 0)
          awayTeam.d = (awayTeam.d || 0) + 1
          awayTeam.w = (awayTeam.w || 0)
          awayTeam.l = (awayTeam.l || 0)
        }
        homeTeam.gf = (homeTeam.gf || 0) + homeTeamResult
        homeTeam.ga = (homeTeam.ga || 0) + awayTeamResult
        homeTeam.dif = 0 + (homeTeam.gf - homeTeam.ga)
        awayTeam.gf = (awayTeam.gf || 0) + awayTeamResult
        awayTeam.ga = (awayTeam.ga || 0) + homeTeamResult
        awayTeam.dif = 0 + (awayTeam.gf - awayTeam.ga)
        homeTeam.pts = (3 * homeTeam.w) + (1 * homeTeam.d) + (0 * homeTeam.l)
        awayTeam.pts = (3 * awayTeam.w) + (1 * awayTeam.d) + (0 * awayTeam.l)
      }
    }
  }
}
</script>

<style scoped>
.team-flag {
  border-style: solid;
  border-width: 3px;
  border-color: #E5C685;
  border-radius: 5px;
  width: 32px;
  height: 22px;
  margin: 5px;
}
h1 .md-title {
  font-size: 30px !important;
  text-align: center !important;
}
.md-table-cell {
  font-size: 1.3em;
}
.md-table-head {
  font-size: 1.3em;
  font-weight: normal;
  color: #015386 !important;
}
.fixed-space {
  width: 10px !important;
}
h1 {
  font-size: 7em;
  line-height: 1em;
  text-transform: uppercase;
}
.align-center {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  max-width: 1280px !important;
  width: 100% !important;
}
.section-gold {
  background-color: #F5EED5;
}
.section-blue {
  background:
    linear-gradient(
      rgba(1, 83, 134, 0.85),
      rgba(1, 83, 134, 0.85)
    ),
    url(http://cdn.welcome2018.com//upload/longreads/2310989/img/tild6339-3063-4766-a332-616639393330__1920x1080_pearl_flat.png?15241335931291351) repeat;
}
.secondary-tittle {
  color: #E5C685 !important;
}
.md-card {
  margin-bottom: 2vh;
}
.sub-tittle {
  color: #015386;
}
.md-layout.md-gutter>.md-layout-item {
  padding: 0px;
}
.footer {
  text-align: center
}
</style>



