<template>
  <div class="section-blue">
    <div class="md-layout md-gutter md-alignment-center">
      <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
        <h1 class="secondary-tittle">Stadiums</h1>
      </div>
    </div>
    <div class="md-layout md-gutter md-alignment-center">
      <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
        <div class="md-layout md-gutter md-alignment-center align-cards">
          <div class="md-layout-item md-size-25 md-large-size-25 md-medium-size-50 md-small-size-50 md-xsmall-size-100 stadium-card" v-for="stadium in stadiums" :key="stadium.id">
            <md-card>
              <md-card-area>
                <md-card-media md-ratio="16:9">
                  <img :src="stadium.img" alt="thumbnail" onerror="this.src='http://minsvyaz.ru/uploaded/photos/fifa.jpg';">
                </md-card-media>

                <md-card-header>
                  <div class="md-title">{{ stadium.name }}</div>
                  <div class="md-subhead">{{ stadium.city }}</div>
                </md-card-header>

                <md-card-content>
                  <div><span class="sub-tittle">Location: </span>{{ stadium.location }}</div>
                  <div><span class="sub-tittle">Project: </span>{{ stadium.project }}</div>
                  <div><span class="sub-tittle">Capacity: </span>{{ stadium.capacity }}</div>
                  <div><span class="sub-tittle">Opened: </span>{{ stadium.opened }}</div>
                  <div><span class="sub-tittle">Cost: </span>{{ stadium.cost | formatCost }}</div>
                </md-card-content>
              </md-card-area>
              <md-card-actions md-alignment="right">
                <md-button :href="stadium.link" class="md-primary">More Info</md-button>
              </md-card-actions>
            </md-card>
          </div>
        </div>
      </div>
    </div>
    <div class="section-gold">
      <div class="md-layout md-gutter md-alignment-center">
        <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
          <Footer></Footer>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import Footer from './Footer'

export default {
  name: 'Stadiums',
  components: {
    Footer
  },
  data () {
    return {
      stadiums: [],
      errors: [],
      counter: 0
    }
  },
  created () {
    Axios.get(`https://api.myjson.com/bins/142y2e`)
      .then(response => {
        this.stadiums = response.data.stadiums
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  filters: {
    formatCost: function (value) {
      return '$' + value
    }
  }
}
</script>

<style scoped>
.stadium-card {
  margin-top: 10px;
  margin-bottom: 25px;
}
h1 {
  font-size: 7em;
  line-height: 1em;
  text-transform: uppercase;
}
.align-center {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  max-width: 1280px !important;
  width: 100% !important;
}
.section-gold {
  background-color: #F5EED5;
}
.section-blue {
  background:
    linear-gradient(
      rgba(1, 83, 134, 0.85),
      rgba(1, 83, 134, 0.85)
    ),
    url(http://cdn.welcome2018.com//upload/longreads/2310989/img/tild6339-3063-4766-a332-616639393330__1920x1080_pearl_flat.png?15241335931291351) repeat;
}
.secondary-tittle {
  color: #E5C685 !important;
}
.md-card-media img {
  width: 100%;
}
.md-card {
  margin-bottom: 2vh;
}
.md-card-content {
  font-size: 18px;
  min-height: 156px;
}
.md-button{
  font-size: 18px !important;
}
.md-card .md-subhead {
  font-size: 16px;
}
.sub-tittle {
  color: #015386;
}
.align-cards {
  align-items: start !important;
  margin: 10px;
}
.md-layout.md-gutter>.md-layout-item {
  padding: 0px;
}
.footer {
  text-align: center
}
</style>



