<template>
    <div class="father-div">
        <div class="md-layout md-gutter">
            <div class="md-layout-item md-size-100">
                <GmapMap
                    :center="center"
                    :zoom="zoom"
                    :md-elevation="3"
                    class="google-map"
                    id="my-map"
                    >
                    <GmapMarker
                        :key="index"
                        v-for="(m, index) in markers"
                        :position="m.position"
                        :clickable="true"
                        :draggable="true"
                        @click="center=m.position"
                    />
                </GmapMap>
            </div>
        </div>
        <div class="md-layout md-gutter">
            <div class="md-layout-item md-size-100">
                <md-menu md-size="medium" md-align-trigger>
                    <md-button md-menu-trigger class='md-raised md-accent select-button'>Select the Stadium</md-button>

                    <md-menu-content>
                        <md-menu-item v-for="stadium in stadiums" :key="stadium.id"  @click="goToLocation(stadium.lat, stadium.lng)">{{ stadium.name }}</md-menu-item>
                    </md-menu-content>
                </md-menu>
            </div>
        </div>
    </div>
</template>

<script>
import Axios from 'axios'

export default {
  name: 'Locations',
  data () {
    return {
      stadiums: [],
      center: { lat: 55.7558, lng: 37.6173 },
      markers: [],
      zoom: 3
    }
  },
  created () {
    Axios.get(`https://raw.githubusercontent.com/lsv/fifa-worldcup-2018/master/data.json`)
      .then(response => {
        this.stadiums = response.data.stadiums
        this.populate(this.stadiums)
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  methods: {
    populate: function (stadiums) {
      for (let stadium of stadiums) {
        const marker = {
          lat: stadium.lat,
          lng: stadium.lng
        }
        this.markers.push({ position: marker })
      }
    },
    goToLocation: function (loclat, loclong) {
      const marker = {
        lat: loclat,
        lng: loclong
      }
      this.center = marker
      this.zoom = 14
    }
  }
}
</script>

<style scoped>
.google-map {
    width: 100%;
    height: 500px;
    margin-bottom: 25px;
}
.father-div {
    width: 100%;
}
.select-button{
    font-size: 18px !important;
    color: #015386 !important;
    margin-bottom: 25px;
}
</style>



