import Vue from 'vue'
import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css'
import VueRouter from 'vue-router'
import * as VueGoogleMaps from 'vue2-google-maps'

import App from './App'
import Home from './components/Home.vue'
import Matches from './components/Matches.vue'
import Stadiums from './components/Stadiums.vue'
import Standings from './components/Standings.vue'

Vue.use(VueMaterial)
Vue.use(VueRouter)

Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyBwkD9aL_kJwNt57GjL9gpQyX3IuteNppI',
    libraries: 'places'
  }
})

const routes = [
  { path: '/', name: 'home', component: Home, props: true },
  { path: '/Matches', name: 'matches', component: Matches, props: true },
  { path: '/Stadiums', name: 'stadiums', component: Stadiums, props: true },
  { path: '/Standings', name: 'standings', component: Standings, props: true }
]

const router = new VueRouter({
  routes
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')



