<template>
  <div class="page-container">
    <md-app md-mode="reveal">
      <md-app-toolbar class="md-primary">
        <md-button class="md-icon-button" @click="menuVisible = !menuVisible">
          <md-icon>menu</md-icon>
        </md-button>
        <span class="md-title">{{$route.params.tittle}}</span>
      </md-app-toolbar>

      <md-app-drawer :md-active.sync="menuVisible">
        <md-toolbar class="md-transparent menu-tittle" md-elevation="0">Menu</md-toolbar>

        <md-list>
          <md-list-item>
            <router-link :to="{ name: 'home', params: {tittle: 'Home'} }" v-on:click.native="menuVisible = !menuVisible" class="menu-link">Home</router-link>
          </md-list-item>

          <md-list-item>
            <router-link :to="{ name: 'matches', params: {tittle: 'Matches'} }" v-on:click.native="menuVisible = !menuVisible" class="menu-link">Matches</router-link>
          </md-list-item>

          <md-list-item>
            <router-link :to="{ name: 'stadiums', params: {tittle: 'Stadiums'} }" v-on:click.native="menuVisible = !menuVisible" class="menu-link">Stadiums</router-link>
          </md-list-item>

          <md-list-item>
            <router-link :to="{ name: 'standings', params: {tittle: 'Standings'} }" v-on:click.native="menuVisible = !menuVisible" class="menu-link">Standings</router-link>
          </md-list-item>
        </md-list>
      </md-app-drawer>

      <md-app-content>
        <router-view></router-view>
      </md-app-content>
    </md-app>
  </div>
</template>

<script>

export default {
  name: 'WorldCupRussiaApp',
  data: () => ({
    menuVisible: false
  })
}
</script>

<style scoped>
@font-face {
  font-family: bebasNeue;
  src: url(../static/fonts/BebasNeue_Regular.otf);
}
.page-container {
  font-family: bebasNeue,Roboto,Noto Sans,-apple-system,BlinkMacSystemFont,sans-serif;
}
.md-drawer {
  width: 230px;
  max-width: calc(100vw - 125px);
}
.md-app-content {
  padding: 0px !important;
}
.menu-tittle {
  color: #015386 !important;
  font-weight: bold;
  font-size: 24px;
}
.menu-link {
  font-size: 20px;
  color: #0074B1;
  text-decoration: none !important;
}
.menu-link:hover {
  color: #b2cbda !important;
}
</style>



