<template>
    <div class="md-layout md-gutter">
      <div class="md-layout-item md-layout md-gutter">
        <div class="md-layout-item"></div>
        <div class="md-layout-item">
          <p class="digit">{{ days }}</p>
          <p class="text">Days</p>
        </div>
        <div class="md-layout-item">
          <p class="digit">:</p>
        </div>
        <div class="md-layout-item">
          <p class="digit">{{ hours }}</p>
          <p class="text">Hours</p>
        </div>
        <div class="md-layout-item">
          <p class="digit">:</p>
        </div>
        <div class="md-layout-item">
          <p class="digit">{{ minutes }}</p>
          <p class="text">Minutes</p>
        </div>
        <div class="md-layout-item"></div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'Countdown',
  data () {
    return {
      now: Math.trunc((new Date()).getTime() / 1000)
    }
  },
  props: {
    date: {
      type: String
    }
  },
  mounted () {
    window.setInterval(() => {
      this.now = Math.trunc((new Date()).getTime() / 1000)
    }, 1000)
  },
  computed: {
    convertDate: function () {
      return Math.trunc(Date.parse(this.date) / 1000)
    },
    minutes () {
      return (Math.trunc((this.convertDate - this.now) / 60) % 60).toString()
    },
    hours () {
      return (Math.trunc((this.convertDate - this.now) / 60 / 60) % 24).toString()
    },
    days () {
      return (Math.trunc((this.convertDate - this.now) / 60 / 60 / 24)).toString()
    }
  }
}
</script>

<style scoped>
.text {
    color: #0074B1;
    font-size: 3em;
    text-align: center;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
}
.digit {
    color: #015386;
    font-size: 10em;
    text-align: center;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
}
@media only screen and (max-width: 425px) {
    .text {
      font-size: 2em;
    }
    .digit {
      font-size: 5em;
    }
}
</style>



