<template>
    <div>
        <div class="md-layout md-gutter">
            <div class="md-layout-item md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="news in latestnews" :key="news.title">
                <md-card>
                    <md-card-area>
                        <md-card-media md-ratio="16:9">
                            <img :src="news.urlToImage" alt="thumbnail" onerror="this.src='http://minsvyaz.ru/uploaded/photos/fifa.jpg';">
                        </md-card-media>

                        <md-card-header>
                            <div class="md-title">{{ news.title }}</div>
                            <div class="md-subhead">{{ format(news.publishedAt, news.author) }}
                                <div>Source: <a :href="formatLink(news.source.name)">{{ news.source.name }}</a></div>
                            </div>
                        </md-card-header>

                        <md-card-content>{{ summarize(news.description) }}</md-card-content>
                    </md-card-area>

                    <md-card-actions md-alignment="right">
                        <md-button :href="news.url" class="md-primary">Learn More</md-button>
                    </md-card-actions>
                </md-card>
            </div>
        </div>
        <div class="md-layout md-gutter md-alignment-center">
            <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
                <md-content :md-elevation="3" class="form-card">
                    <div class="spacing">
                        <h2>News Search Filters</h2>
                        <md-field>
                            <label for="domains" id="label">Sources Available</label>
                            <md-select v-model="selectedDomains" name="domains" id="domains" multiple>
                                <md-option v-for="domain in domainsAvailable" :key="domain.id" :value="domain.link">{{ domain.name }}</md-option>
                            </md-select>
                        </md-field>
                        <md-chip class="md-primary" v-for="chip in selectedDomains" :key="chip">{{ chip }}</md-chip>
                        <div class="sort-radio">
                            <span>Sort Order: </span>
                            <md-radio v-for="so in sortOrdersAvailable" :key="so.id" v-model="sortBy" :value="so.value" class="md-primary">{{ so.name }}</md-radio>
                        </div>
                        <md-button class="md-raised md-primary" @click="searchNews"><md-icon>search</md-icon>Search News</md-button>
                        <md-button class="md-raised md-primary" @click="change"><md-icon>arrow_forward</md-icon>Next News</md-button>
                    </div>
                </md-content>
            </div>
        </div>
        <div class="md-layout md-gutter md-alignment-center">
            <div class="md-layout-item md-medium-size-100 md-small-size-100 md-xsmall-size-100 align-center">
                <md-button class="md-raised md-accent reload-button" href="https://newsapi.org/">News API</md-button>
            </div>
        </div>
        <md-snackbar :md-position="position" :md-duration="isInfinity ? Infinity : duration" :md-active.sync="showSnackbar" md-persistent>
            <span>Sorry, you have viewed all the news at the moment!</span>
            <md-button class="md-primary" @click="showSnackbar = false">Retry</md-button>
        </md-snackbar>
    </div>
</template>

<script>
import Axios from 'axios'

export default {
  name: 'News',
  data () {
    return {
      url: '',
      query: '%22fifa%20world%20cup%22',
      complete: [],
      latestnews: [],
      errors: [],
      begin: 0,
      end: 3,
      showSnackbar: false,
      position: 'center',
      duration: 4000,
      isInfinity: false,
      selectedDomains: [],
      domainsAvailable: [
        {id: 1, name: 'FIFA', link: 'fifa.com'},
        {id: 2, name: 'ESPN', link: 'espn.com'},
        {id: 3, name: 'Bleacher Report', link: 'bleacherreport.com'},
        {id: 4, name: 'CBS Sports', link: 'cbssports.com'},
        {id: 5, name: 'Sports Illustrated', link: 'si.com'},
        {id: 6, name: 'SB Nation', link: 'sbnation.com'},
        {id: 7, name: 'FOX Sports', link: 'foxsports.com'}
      ],
      sortBy: 'publishedAt',
      sortOrdersAvailable: [
        {id: 1, name: 'Date', value: 'publishedAt'},
        {id: 2, name: 'Popularity', value: 'popularity'},
        {id: 3, name: 'Relevancy', value: 'relevancy'}
      ],
      selectedDate: null
    }
  },
  created () {
    this.url = 'https://newsapi.org/v2/everything?q=%22fifa%20world%20cup%22&domains=fifa.com&language=en&from=2018-02-01&sortBy=publishedAt&pageSize=15&apiKey=d131c106b4524dbc871836f41c9daa5f'
    this.getNews(this.url)
  },
  methods: {
    change: function () {
      if (this.begin < 15) {
        this.latestnews = this.complete.slice(this.begin, this.end)
        this.begin += 3
        this.end += 3
      } else {
        this.showSnackbar = true
      }
    },
    format: function (date, author) {
      let time = new Date(date) + ''
      let v = time.split(' ')
      let formated = 'By: ' + author + ' on ' + v[2] + '/' + v[1] + '/' + v[3] + ' - ' + v[4]
      return formated
    },
    getNews: function (url) {
      Axios.get(url, { crossdomain: true })
        .then(response => {
          this.complete = response.data.articles
          this.change()
        })
        .catch(e => {
          this.errors.push(e)
        })
    },
    summarize: function (str) {
      if (str.length === 0) {
        return 'No description available'
      } else {
        let original = str.split(' ')
        let final = ''
        let size = 20
        if (original.length < size) {
          size = original.length
        }
        for (let i = 0; i < size; i++) {
          final += original[i] + ' '
        }
        return final + ' ...'
      }
    },
    formatLink: function (link) {
      return 'http://' + link
    },
    searchNews: function () {
      let base = 'https://newsapi.org/v2/everything?q='
      let sources = ''
      for (let i = 0; i < this.selectedDomains.length; i++) {
        sources += this.selectedDomains[i] + ','
      }
      let url = base + this.query + '&domains=' + sources + '&language=en&from=2018-02-01&sortBy=' + this.sortBy + '&pageSize=15&apiKey=d131c106b4524dbc871836f41c9daa5f'
      this.begin = 0
      this.end = 3
      this.getNews(url)
    }
  }
}
</script>

<style scoped>
.md-card-media img {
    width: 100%;
}
.md-card {
    margin-bottom: 2vh;
}
.md-card-header .md-title {
    min-height: 64px;
}
.md-card-content {
    font-size: 18px;
    min-height: 100px;
}
.md-button {
    font-size: 18px !important;
}
.reload-button {
    color: #015386 !important;
}
.spacing {
    padding: 25px;
    margin-bottom: 25px;
    margin-top: 15px;
}
h2 {
    color: #015386;
    font-size: 30px;
}
.md-field .md-has-value .md-input {
    font-size: 20px;
}
label {
    font-size: 20px;
}
.form-card {
    margin-left: 12px;
    margin-right: 12px;
}
.md-chip {
    font-size: 16px;
}
.sort-radio {
    font-size: 20px;
}
.sort-radio span {
    padding-right: 10px;
    color: rgba(0,0,0,0.6);
}
</style>



