<template>
  <div>
    <div class="md-layout md-gutter">
      <div class="md-layout-item md-size-25 md-large-size-25 md-medium-size-25 md-small-size-25 md-xsmall-size-50 group-card" v-for="group in groups" :key="group.id">
        <div class="viewport">
          <md-toolbar :md-elevation="1" class="md-primary">
            <span class="md-title">{{ group.name }}</span>
          </md-toolbar>

          <md-list>
            <md-list-item v-for="team in group.teams" :key="team.id">
              <img :src="team.flag" :alt="team.emoji" class="team-flag">
              <span class="md-list-item-text">{{ team.name }}</span>
            </md-list-item>
          </md-list>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'

export default {
  name: 'Groups',
  data () {
    return {
      teams: [],
      groups: [],
      errors: [],
      letters: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H'],
      counter: 0,
      begin: 0,
      end: 4
    }
  },
  created () {
    Axios.get(`https://raw.githubusercontent.com/lsv/fifa-worldcup-2018/master/data.json`)
      .then(response => {
        this.teams = response.data.teams
        this.populate(this.teams)
      })
      .catch(e => {
        this.errors.push(e)
      })
  },
  methods: {
    populate: function (dataTeams) {
      for (let i = 0; i < this.letters.length; i++) {
        let tittle = 'Group ' + this.letters[this.counter]
        this.groups.push({id: this.counter, name: tittle, teams: dataTeams.slice(this.begin, this.end)})
        this.counter++
        this.begin += 4
        this.end += 4
      }
    }
  }
}
</script>

<style scoped>
.team-flag {
  border-style: solid;
  border-width: 3px;
  border-color: #E5C685;
  border-radius: 5px;
  width: 32px;
  height: 22px;
  margin: 5px;
}
.group-card {
  margin-top: 10px;
  margin-bottom: 25px;
}
</style>



